const ADD_MASSAGE = 'ADD-MASSAGE';
const DELETE_MASSAGE = 'DELETE-MASSAGE';

let initialeState = {
   dialogs: [
      { id: 0, name: '–ß—ë—Ä–Ω—ã–π –≤–ª–∞—Å—Ç–µ–ª–∏–Ω' },
      { id: 1, name: '–ò–≤–∞–Ω' },
      { id: 2, name: '–®–∫–∞–ª—å–Ω–∏–∫' },
      { id: 3, name: '–Ø–Ω' },
      { id: 4, name: '–ê–ª–µ–∫—Å–∞–Ω–¥—Ä' },
      { id: 5, name: '–ù–∏–∫–æ–ª–∞–π' },
      { id: 6, name: '–ü–ö' },
      { id: 7, name: '–ë–µ–ª—ã–π –≤–ª–∞—Å—Ç–µ–ª–∏–Ω' },
      { id: 8, name: '–õ–µ–Ω—å –±—ã–ª–æ –∏–º—è –ø—Ä–∏–¥—É–º—ã–≤–∞—Ç—å' },
   ],
   massages: [
      { id: 1, massage: "–ü—Ä–∏–≤–µ—Ç" },
      { id: 2, massage: "–°–æ–æ–±—â–µ–Ω–∏–µ" },
      { id: 3, massage: "1234" },
   ]
}

const dialogsReducer = (state = initialeState, action) => {
   switch (action.type) {
      case ADD_MASSAGE: {
         let newMassage = {
            id: state.massages.length + 1,
            massage: action.text,
         }
         return {
            ...state,
            massages: [...state.massages, newMassage],
         };
      }

      case DELETE_MASSAGE: {
         return {
            ...state,
            massages: [...state.massages.filter(p => p.id != action.id)],
         };
      }
      default:
         return state;
   }
}

export const addMassageActionCreator = (text) => {
   return {
      type: ADD_MASSAGE, text: text,
   }
}
export const deleteMassageActionCreator = (messageId) => {
   return {
      type: DELETE_MASSAGE, id: messageId,
   }
}

export default dialogsReducer;